import React from 'react' 
import './services.css' 
import {BiCheck} from 'react-icons/bi'

const services = () => {
  return (
    <section id="service"> 
    <h5>O que eu Ofereço?</h5> 
    <h2>Diferenciais</h2> 

    <div className="container service_container"> 
      <article className="service"> 
        <div className="service_head"> 
          <h3>UI/UX Design</h3> 
        </div> 

        <ul className="service_list"> 
          <li> 
            <BiCheck className='service_list_icon' /> 
            <p>Conhecimentos em estrutura de Interface Web.</p> 
          </li> 
          <li> 
            <BiCheck className='service_list_icon' /> 
            <p>Interação com Usuários.</p> 
          </li> 
        </ul> 

      </article> 
      {/*END UI/UX */} 

      <article className="service"> 
        <div className="service_head"> 
          <h3>Web Developer</h3> 
        </div> 

        <ul className="service_list"> 
          <li> 
            <BiCheck className='service_list_icon' /> 
            <p>Integração de API's.</p> 
          </li> 
          <li> 
            <BiCheck className='service_list_icon' /> 
            <p>Hospedagem de Site.</p> 
          </li> 
          <li> 
            <BiCheck className='service_list_icon' /> 
            <p>Controle de Versionamento Git.</p> 
          </li> 
        </ul> 

      </article> 
      {/* END WEB DEVELOPER*/}  

      <article className="service"> 
        <div className="service_head"> 
          <h3>Profissionalismo</h3> 
        </div> 

        <ul className="service_list"> 
          <li> 
            <BiCheck className='service_list_icon' /> 
            <p>Detalhista.</p> 
          </li> 
          <li> 
            <BiCheck className='service_list_icon' /> 
            <p>Atento aos Prazos.</p> 
          </li> 
          <li> 
            <BiCheck className='service_list_icon' /> 
            <p>Leitura de Documentação.</p> 
          </li>  
          <li> 
            <BiCheck className='service_list_icon' /> 
            <p>Autodidata.</p> 
          </li> 
        </ul> 

      </article>   
       {/* END CONTEUDO CRIATIVO*/}  

    </div>
    </section>
  )
}

export default services