import React from 'react' 
import './portifolio.css' 
import IMG1 from '../../assets/Pokedex.jpg'
import IMG2 from '../../assets/P.portifolio.jpg'
import IMG3 from '../../assets/MSocial.jpg'
 
const data = [ 
  { 
    id: 1, 
    image: IMG1, 
    title: 'PokeDex', 
    github: 'https://github.com/Maleao/PokedeX_JS',
    link: ' https://maleao.github.io/PokedeX_JS/',
  }, 
  { 
    id: 2, 
    image:IMG2,
    title: 'Porifólio', 
    github: 'https://github.com/Maleao/Project-Portif-lio',
    link: 'https://maleao.github.io/Project-Portif-lio/',
  }, 
  { 
    id: 3, 
    image:IMG3, 
    title: 'Social Media', 
    github: 'https://github.com/Maleao/SocialMedia',
    link: 'https://maleao.github.io/SocialMedia/ '
  } 
]


const Porifolio = () => {
  return (
    <section id='portifolio'> 
    <h5>Meus Trabalhos Recentes</h5> 
    <h2>Portifólio</h2> 

    <div className='container portifolio_container'> 
      {  
        data.map(({id, image, title, github, link}) => { 
          return( 
          <article key={id} className="portifolio_item"> 
          <div className="portifolio_item_image"> 
            <img src={image} alt={title}/> 
          </div>
             <h3>{title}</h3> 
             <div className='portifolio_item_cta'>
               <a href={github} className='btn' target='_blank'>GitHub</a>
               <a href={link} className='btn btn-primary' target='_blank'>Demonstração</a>
             </div>
        </article> 
          )
        })
      }
    </div> 
    </section>
  )
}

export default Porifolio