import React from 'react' 
import './header.css' 
import CTA from './CTA' 
import ME from '../../assets/me.png' 
import HeaderSocials from './HeaderSocials'

const Header = () => {
  return (
    <header> 
      <div className="container header_container"> 
        <h5>Olá, Eu sou</h5> 
        <h1>Matheus Leão</h1> 
        <h5 className="text-light"> 
         Desenvolvedor Front-End
        </h5> 
        <CTA /> 
        <HeaderSocials /> 
         
        <div className="me"> 
          <img src={ME} alt="mim" />  
        </div> 
         
        <a href="#contact" className='scroll_down'>Rolar para Baixo</a>
      </div>
    </header>
  )
}

export default Header